@import url('https://fonts.googleapis.com/css2?family=Playfair:ital,opsz,wght@0,5..1200,300..900;1,5..1200,300..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/* font-family: 'Poppins',sans-serif; */
/* font-family: "Playfair", serif; */


.navbar{
    font-family: 'Poppins',sans-serif;
    font-size: 17px;
    line-height: 17px;
    text-transform: uppercase;
    box-shadow: 0 8px 6px -6px gray;
    --webkit-box-shadow:0 8px 6px -6px gray;
    border-bottom: solid gray !important;
}
.nav-link{
    font-weight: 300 !important;
}
.active{
    border-bottom: 2px solid black;
}
.navbar-brand{
    font-weight: 700;
    font-family: 'roboto',sans-serif;
    letter-spacing: 3px;
}


/* footer */
.footer{
    color: white;
    padding: 25px;
    background-color: #000000;
    background:--webkit-linear-gradient(
        to right,
        #434343,
        #000000
    );
    background: linear-gradient(to right,#434343,#000000);
} 
.footer a{
    text-decoration: none;
    color: white;
    padding: 10px;
}   
.footer a:hover{
    color: #ffefba;
    border-bottom: 2px solid #ffefba;
}
/* ==================Pagenotfound======================== */
.pnf{
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.pnf-title{
    font-size: 100px;
    font-weight: 700;
}
.pnf-heading{
    font-weight: normal;
}
.pnf-btn{
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
}


/* =============Contactus========================== */
.contactus img{
    width: 400px;
    height: 400px;
    margin-top: 100px;
}
.contact-box{
    margin-top: 150px;
    margin-left: 50px;
    line-height: 40px;
}




/* ================Privacy Policy=============== */
.policy{
    display: flex;
    align-items: center;
    justify-content: center;
}
.policy img{
    width: 400px;
    height: 400px;
    margin-top: 100px;
    border-radius: 5px;
}
.policy-box{
    margin-top: 150px;
    margin-left: 150px;
    line-height: 40px;
}


/* ==============Register===================== */
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    flex-direction: column;
    background-color: #ffdee9;
    background-image: linear-gradient(0deg,#ffdee9 0%,#b5fffc 100%);
}
.form-container form{
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    padding: 20px;
    background-color: #fff;
}
.form-container form .title{
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: 'Playfair Display',serif;
}
.form-container form input{
    bottom: none;
    border-bottom: 1px solid black;
    border-radius: 0;
}
.form-container form button{
    border: 1px solid black;
    border-radius: 0;
    background-color: #000;
    color: white;
    width: 250px;
}
.form-container form button:hover{
    border: 1px solid black;
    background: #000000;
    background: -webkit-linear-gradient(
        to right,
        #434343,
        #000000
    );
    background: linear-gradient(
        to right,
        #434343,
        #000000
    );
}

.product-link{
    text-decoration: none !important;
    color: black !important;
}
.productimg{
    height: 17rem;
}

#special-offer{
    width: 84rem;
    height: 26rem;
    border-radius: 7px;
}
#img-detail{
    width: 30rem;
    height: 20rem;
    margin-top: 24px;
}
